<template>
	<div class="pdawrap">
		<el-page-header @back="goBack" title="" :content="$t('pda.detail')"></el-page-header>
		<!-- <el-scrollbar style='height:100%;overflow-x: hidden'>  -->
		<div class="pdalight">
			<div style="display:flex">
				<div class="light_title">
					<span>{{$t('storage.lights_tip')}}</span>
				</div>
				<div class="light_list">
					<span :class="this.$store.state.pdacolor==3?'light_three_active':'light_three' " @click="colorThree()"></span>
					<span :class="this.$store.state.pdacolor==4?'light_four_active':'light_four' "   @click="colorFour()"></span>
					<span :class="this.$store.state.pdacolor==5?'light_five_active':'light_five' "   @click="colorFive()"></span>
					<span :class="this.$store.state.pdacolor==6?'light_six_active':'light_six' "   @click="colorSix()"></span>
				</div>
			</div>
		</div>
		<div class="table_div" v-show="ifhidden">
			<table class="labeltable">
				<tr class="label_tr">
          <td class="title_td">{{$t('storage.label_code')}}</td>
          <td class="content_td">{{this.row.label_code}}</td>
        </tr>
         <tr>
          <td class="title_td">{{$t('storage.quantity')}}</td>
          <td class="content_td">{{this.row.quantity}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.part_num')}}</td>
          <td class="content_td">{{this.row.part_num}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.customer_name')}}</td>
          <td class="content_td">{{this.row.customer}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.supplier_name')}}</td>
          <td class="content_td">{{this.row.supplier_name}}</td>
        </tr>      
        <tr>
          <td class="title_td">{{$t('storage.shelf_id')}}</td>
          <td class="content_td">{{this.row.shelf_id}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.position')}}</td>
          <td class="content_td">{{this.row.position}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.description')}}</td>
          <td class="content_td">{{this.row.description}}</td>
        </tr>
         <tr>
          <td class="title_td">{{$t('storage.mfg_date')}}</td>
          <td class="content_td">{{this.row.mfg_date}}</td>
        </tr>
			</table>
		</div>	
		<div class="orderid_div">
			<el-input v-model="order_id" :placeholder="this.$t('pda.ordertip')" clearable @focus="focus" @blur="blur"></el-input>
		</div>
		<div class="choose_div">
			<span class="searchout_title">{{$t('storage.category_name')}}</span>
			<template>
		  	<el-select value-key="id" v-model="warehouse_category_id" @change="categoryTypeChange">
			    	<el-option
			      	v-for="item in categoryTypeList"
			      	:key="item.id"
			      	:label="item.category_name"
			      	:value="item.id">										      
			    	</el-option>
					</el-select>
			</template>
		</div>
		<div class="choose_div">
				<span class="searchout_title">{{$t('storage.warehouse_name')}}</span>
				<template>
			  	<el-select value-key="id" v-model="warehouse_name_id" >
		    	<el-option
			      	v-for="item in warehouselist"
			      	:key="item.id"
			      	:label="item.warehouse_name"
			      	:value="item.id">										      
		    	</el-option>
				</el-select>
				</template>
			</div>
		<div class="orderid_div">
			<el-button type='primary' @click="takeOut" :disabled="this.row.state? true:false">{{$t('pda.take_out')}}</el-button>
		</div>
		<!-- </el-scrollbar> -->
	</div>
</template>
<script>
import { mapMutations } from 'vuex'
import { outByPosition } from '@/request/api/material'
import {searchWarehouseCategory, searchWarehouseNameByCategory } from '@/request/api/materialshelf'
	export default{
		data(){
			return{
				row:this.$store.state.row,
				ifhidden:true,
				order_id:'',
				warehouse_category_id:0,
				warehouse_name_id:0,
				categoryTypeList:[{'category_name':'所有','id':0}],
				warehouselist:[{'warehouse_name':'所有','id':0}],
			}
		},
		mounted(){
			this.getCategoryType()
		},
		methods:{
			...mapMutations(['pdaColor']),
			colorThree(key){
				this.pdaColor({ pdacolor: 3});		
			},
			colorFour(key){
				this.pdaColor({ pdacolor: 4});	
			},
			colorFive(key){
				this.pdaColor({ pdacolor: 5});	
			},
			colorSix(key){
				this.pdaColor({ pdacolor: 6});	
			},
			goBack(){
				this.$router.go(-1)
			},
			// 获取仓库类型
	    getCategoryType(){
	    	searchWarehouseCategory({}).then(res =>{
	    		if (res.result) {
	    			this.categoryTypeList = this.categoryTypeList.concat(res.data_list)
	    		}
	    	})
	    },
	    // 获取库位
	    getWarehouseList(){
	    	this.warehouselist = [{'warehouse_name':'所有','id':0}]
	    	this.warehouse_name_id = 0
	    	searchWarehouseNameByCategory({warehouse_category_id:this.warehouse_category_id}).then(res =>{
	    		if (res.result) {
	    			this.warehouselist = this.warehouselist.concat(res.data)
	    		}
	    	})
	    },
	    categoryTypeChange(){
	    	this.warehouse_name_id = 0
	    	this.getWarehouseList()
	    },
	    focus(){
      	this.ifhidden = false
      },
      blur(){
      	this.ifhidden = true
      },
			// 取料
      takeOut(){
      	let dataparams={
					position: this.row.position,
					shelf_id: this.row.shelf_id,
					color: this.$store.state.pdacolor,
					order_id:this.order_id,
					warehouse_name_id:this.warehouse_name_id,
					warehouse_category_id:this.warehouse_category_id
				}
				outByPosition(dataparams).then(res =>{
					if (res.result) {
						this.$message({
              showClose: true,
              message: this.$t('pda.takeout_success'),
              type: 'success'
            })
            this.$router.go(-1)
					}else{
						this.$message({
              showClose: true,
              message: this.$t('pda.takeout_failed'),
              type: 'error'
            })
					}
				})
      }
		}
	}
</script>
<style>
	.table_div{
		margin-top: 0.53rem;
		font-size: 0.4rem;
	}
	.labeltable {
	  border-collapse: collapse;
	  margin: 0 auto;
	  text-align: center;
	  width:100%;
	}
	/*.title_td{
		width:3rem;
	}*/
	.content_td{
		width:5.6rem;
	}
	.orderid_div{
		margin: 0.6rem 0;
	}
	.orderid_div .el-input__inner{
		width:100%;
		height: 1.06rem;
	}
	.orderid_div .el-input{
		width:100%;
		height: 1.06rem;
	}
	.orderid_div .el-button{
		width:100%;
	/*	height: 0.85rem;*/
		font-size: 0.4rem;
	}
	.labeltable td{
		border:0.026rem solid lightgray;
		height: 0.85rem;
		line-height: 0.85rem;
	}
	.table_div .title_td{
		width:40%;
	}
	.table_div .content_td{
		width:60%;
	}
</style>


